.title {
	font-size: 20px;
	line-height: 26px;
	color: #f8f8f8;
	text-transform: uppercase;

	&-withDot{
		position: relative;
		padding-left: 40px;
		&::before {
			position: absolute;
			content: "";
			width: 20px;
			height: 20px;
			border-radius: 50%;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			border: 2px solid #64c1c6;
		}
	}

	&-xl {
		font-size: 24px;
		line-height: 31px;
	}

	&-m {
		font-size: 20px;
		line-height: 26px;
	}

	@media screen and (min-width: 768px) {
		font-size: 35px;
		line-height: 43px;

		&-m {
			font-size: 35px;
			line-height: 45px;
		}
	}

	@media screen and (min-width: 1440px) {
		&-xl {
			font-size: 44px;
			line-height: 56px;
		}

		&-m {
			font-weight: 700;
			font-size: 40px;
			line-height: 41px;
		}
	}
}
